import sendErrorWorker from './webworkers/sendError.worker'
import HelloIndexedDB from 'hello-indexeddb'
let idb = new HelloIndexedDB();
// 使 Error 对象支持 JSON 序列化
if (!('toJSON' in ErrorEvent.prototype)) {
    Object.defineProperty(ErrorEvent.prototype, 'toJSON', {
        value() {
            const alt = {};
            Object.getOwnPropertyNames(this)
                .forEach(function (key) {
                    alt[key] = this[key];
                }, this);
            return alt;
        },
        configurable: true,
        writable: true
    });
}

class MonitorForWeb {
    constructor(param) {
        let config = param;
        if (typeof config === 'undefined') {
            throw new Error('MonitorForWeb初始化错误 - 构造函数的参数不能为空！');
        }
        if (typeof config === 'string') {
            config = {
                url: param,
                maxRetryCount: 5,
                reportingCycle: 100000,
                maxQueueCount: 100,
                moduleName: location.pathname.split('/')[1] || null,
                isLog: true
            };
        } else if (typeof config === 'object') {
            if (typeof param.url !== 'string') {
                throw new Error('MonitorForWeb初始化错误 - 构造函数的参数 url 必须是一个字符串！');
            }
            if (typeof param.maxRetryCount !== 'number') {
                config.maxRetryCount = 5
            }
            if (typeof param.reportingCycle !== 'number') {
                config.reportingCycle = 100000
            }
            if (typeof param.maxQueueCount !== 'number') {
                config.maxQueueCount = 100
            }
            if (typeof param.moduleName !== 'string') {
                config.moduleName = location.pathname.split('/')[1] || null
            }
            if (typeof param.isLog !== 'boolean') {
                config.isLog = true
            }
        } else {
            throw new Error('MonitorForWeb初始化错误 - 构造函数的参数格式不正确！');
        }

        // 日志上报地址
        this.url = config.url;

        // 请求重试次数
        this.retryCount = 1;

        // 日志上报配置
        this.config = config;

        // 是否要格式化 console 打印的内容
        this.stylize = config.stylize == null ? true : config.stylize;

        this.stylize = this.stylize && MonitorForWeb._stylizeSupport();

        // 异常日志队列
        this.queue = [];

        // 点击事件源路径
        this.clickEvents = [];

        // 是否自动记录未捕获错误
        this.autoLogError = config.autoLogError == null ? true : config.autoLogError;

        // 是否自动记录 Promise 错误
        this.autoLogRejection = config.autoLogRejection == null ? true : config.autoLogRejection;

        // 是否自动记录 ajax
        this.autoLogAjax = config.autoLogAjax == null ? true : config.autoLogAjax;

        // 默认的 ajax 自动记录情况过滤
        const defaultLogAjaxFilter = (ajaxUrl, ajaxMethod) => true;

        // ajax 自动记录情况过滤，返回 true 代表要记录日志，false 代表不记录日志
        this.logAjaxFilter = config.logAjaxFilter == null ? defaultLogAjaxFilter : config.logAjaxFilter;

        // xhr 原生 open 方法
        this.xhrOpen = XMLHttpRequest.prototype.open;

        // xhr 原生 send 方法
        this.xhrSend = XMLHttpRequest.prototype.send;

        // 初始化
        this._init()
    }

    _init () {
        // 生成唯一id
        this.getReqId();

        // 自动记录异常
        this.exceptionHandler();

        // 记录点击操作
        this.exceptionClick();

        // 自动记录ajax请求异常
        this.ajaxHandler();

        //清理历史log
        this.clearHistory();

        // 定时检查上报
        this.timer = setInterval(() => {
            this.send()
        }, this.config.reportingCycle)
    }

    // 清除历史log
    async clearHistory () {
        let history = await idb.getItem('MonitorForWeb');
        if (history && history.data.length) {
            this.queue.push(...history.data);
        }
        await idb.delete('MonitorForWeb')
    }
    // 记录点击事件
    exceptionClick () {
        document.addEventListener('click',(e) => {
            let path = [];
            e.path.forEach((item) => {
                path.unshift({
                    nodeName: item.nodeName || 'WINDOW',
                    className: item.className || null,
                    id: item.id || null
                })
            });
            this.clickEvents.unshift({
                x: e.pageX,
                y: e.pageY,
                width: window.innerWidth,
                height: window.innerHeight,
                path: path
            });
            this.clickEvents.splice(0, 30)
        });
    }

    // 自动捕获异常
    exceptionHandler () {
        // 页面onerror捕获异常
        window.addEventListener('error', (error) => {
            setTimeout(() => {
                let log = {
                    type: '[onError]',
                    messages: error.message || null,
                    path: error.filename || null,
                    lineNo: error.lineno || null,
                    columnNo: error.colno || null,
                    error: error.error.stack || null,
                    isTrusted: error.isTrusted || null,
                    time: new Date().getTime(),
                    timeLocalString: MonitorForWeb._getDateTimeString(new Date()),
                    clickEvents: this.clickEvents || null,
                    userAgent: navigator.userAgent || null,
                    moduleName: this.config.moduleName,
                    id: this.reqId + '-' + Number(Math.random().toString().substr(2)).toString(36)
                };
                this.queue.push(log);
            }, 500)
            // return true // <-- 阻止报错向上传递
        });

        // Promise捕获异常
        window.addEventListener('unhandledrejection', (event) => {
            let log = {
                type: '[onError]',
                messages: event.reason || null,
                path: window.location.href || null,
                errorType: 'PromiseError',
                userAgent: navigator.userAgent || null,
                time: new Date().getTime(),
                timeLocalString: MonitorForWeb._getDateTimeString(new Date()),
                clickEvents: this.clickEvents || null,
                moduleName: this.config.moduleName,
                id: this.reqId + '-' + Number(Math.random().toString().substr(2)).toString(36)
            };
            this.queue.push(log);
        });
    }

    // 解析 url
    static resolveUrl(url) {
        const link = document.createElement('a');
        link.href = url;
        return `${link.protocol}//${link.host}${link.pathname}${link.search}${link.hash}`;
    }

    // 自动记录ajax请求异常
    ajaxHandler() {
        if (this.autoLogAjax) {
            const that = this;

            // 重写 open 方法
            XMLHttpRequest.prototype.open = function (...args) {
                this._MonitorForWebMethod = args[0];
                this._MonitorForWebUrl = MonitorForWeb.resolveUrl(args[1]);
                that.xhrOpen.apply(this, args);
            };

            // 重写 send 方法
            XMLHttpRequest.prototype.send = function (data) {
                // 请求开始时间
                const startTime = new Date();

                // 添加 readystatechange 事件
                this.addEventListener('readystatechange', function () {
                    // 排除掉用户自定义不需要记录日志的 ajax
                    if (that.logAjaxFilter(this._MonitorForWebUrl, this._MonitorForWebMethod)) {
                        try {
                            if (this.readyState === XMLHttpRequest.DONE) {
                                // 请求结束时间
                                const endTime = new Date();

                                // 请求耗时
                                const costTime = (endTime - startTime) / 1000;

                                const messages = [];
                                if (this.status >= 200 && this.status < 400) {
                                    messages.push('接口请求成功。');
                                } else {
                                    messages.push('接口请求失败！');
                                }
                                messages.push(`请求耗时：${costTime}s URL：${this._MonitorForWebUrl} 请求方式：${this._MonitorForWebMethod}`);
                                if (this._MonitorForWebMethod.toLowerCase() === 'post') {
                                    messages.push('表单数据：', data);
                                }
                                messages.push(`状态码：${this.status}`);
                                if (this.status >= 200 && this.status < 400) {
                                    // 暂不记录请求成功的状态
                                    // that.info('[ajax]', ...messages);
                                } else {
                                    that.error('[ajax]', ...messages);
                                }

                                if (console && console.group) {
                                    console.groupEnd();
                                }
                            }
                        } catch (err) {
                            const messages = [];
                            messages.push('接口请求出错！');
                            messages.push(`URL：${this._MonitorForWebUrl} 请求方式：${this._MonitorForWebMethod}`);
                            if (this._MonitorForWebMethod.toLowerCase() === 'post') {
                                messages.push('表单数据：', data);
                            }
                            messages.push(`状态码：${this.status}`);
                            messages.push(`ERROR：${err}`);
                            that.error('[ajax]', ...messages);
                        }
                    }
                });

                that.xhrSend.call(this, data);
            };
        }
    }

    // 将日志添加到队列中
    pushToQueue(time, level, ...args) {
        args.unshift(`{${this.reqId + '-' + Number(Math.random().toString().substr(2)).toString(36)}}`);
        this.queue.push({
            type: '[ajax]',
            time: MonitorForWeb._getDateTimeString(time),
            moduleName: this.config.moduleName,
            level,
            messages: args,
            url: window.location.href,
            userAgent: navigator.userAgent
        });
    }

    //处理日志队列
    send () {
        if (this.queue && this.queue.length) {
            this.saveError()
        }
    }

    // 存储日志队列
    saveError () {
        // 创建多线程处理队列
        let worker = new sendErrorWorker();

        // 每次最多上报数条
        let data = {
            config: this.config,
            queue: this.queue.splice(0, this.config.maxQueueCount)
        };

        worker.postMessage(JSON.stringify(data));

        worker.onmessage = async (event) => {

            // 上报成功, 清除队列
            if (event.data === MonitorForWeb.workerEnmu.done) {
                this.queue = [];
                this.retryCount = 1
            }

            if (event.data === MonitorForWeb.workerEnmu.retry) {
                if (this.retryCount >= this.config.maxRetryCount) {
                    clearInterval(this.timer);
                    if (this.config.isLog) console.log(`发送日志请求的连续失败次数过多，已停止发送日志。请检查日志接口 ${this.url} 是否正常！`);
                } else {
                    if (this.config.isLog) console.log('配置地址[' + this.config.url + ']上报失败, 等待下次重试 ' + this.retryCount + '...');
                    await idb.delete('MonitorForWeb');
                    this.retryCount ++;
                }
            }

            //会话结束, 释放内存
            worker.terminate();
            worker = null;
        };

    };

    static _stylizeSupport() {
        const isChrome = !!window.chrome;
        const isFirefox = navigator.userAgent.indexOf('Firefox') !== -1;
        return isChrome || isFirefox;
    }
    //获取或者生成唯一请求 id
    getReqId() {
        this.reqId = document.querySelector('[name="_reqId"]') ? document.querySelector('[name="_reqId"]')
            .content : '';
        if (!this.reqId) {
            this.reqId = window._reqId;
        }
        if (this.reqId) {
            // 存在 reqId，说明这是一个服务器端生成的页面，设置一个标示
            this.idFromServer = true;
        } else {
            // 如果不存在 reqId，说明这是一个纯前端的页面，就自己生成一个 reqId https://stackoverflow.com/questions/105034/create-guid-uuid-in-javascript/8809472#8809472
            let time = Date.now();
            if (typeof performance !== 'undefined' && typeof performance.now === 'function') {
                // 使用更高精度的时间
                time += performance.now();
            }
            this.reqId = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, char => {
                const rand = (time + (Math.random() * 16)) % 16 | 0;
                time = Math.floor(time / 16);
                return (char === 'x' ? rand : ((rand & 0x3) | 0x8))
                    .toString(16);
            });
            this.idFromServer = false;
        }
    }

    static _getTimeString(time) {
        const now = (time === null ? new Date() : time);

        // 时
        let hour = String(now.getHours());
        if (hour.length === 1) {
            hour = `0${hour}`;
        }

        // 分
        let minute = String(now.getMinutes());
        if (minute.length === 1) {
            minute = `0${minute}`;
        }

        // 秒
        let second = String(now.getSeconds());
        if (second.length === 1) {
            second = `0${second}`;
        }

        // 毫秒
        let millisecond = String(now.getMilliseconds());
        if (millisecond.length === 1) {
            millisecond = `00${millisecond}`;
        } else if (millisecond.length === 2) {
            millisecond = `0${millisecond}`;
        }

        return `${hour}:${minute}:${second}.${millisecond}`;
    }

    // 获取日期时间字符串
    static _getDateTimeString(time) {
        const now = (time === null ? new Date() : time);

        // 年
        const year = String(now.getFullYear());

        // 月
        let month = String(now.getMonth() + 1);
        if (month.length === 1) {
            month = `0${month}`;
        }

        // 日
        let day = String(now.getDate());
        if (day.length === 1) {
            day = `0${day}`;
        }

        return `${year}-${month}-${day} ${MonitorForWeb._getTimeString(now)}`;
    }

    // 调用系统 console 打印日志
    _printConsole(time, level, ...args) {
        if (console && this.config.isLog) {
            if (this.stylize) {
                console[level](`%c[${MonitorForWeb._getTimeString(time)}] [${level.toUpperCase()}] -`, `color: ${MonitorForWeb.colorEnum[level]}`, ...args);
            } else {
                console[level](...args);
            }
        }
    }
    _log(time, level, ...args) {
        // 调用系统 console 打印日志
        this._printConsole(time, level, ...args);

        // 将日志添加到队列中
        this.pushToQueue(time, level, ...args);
    }

    // 记录一条信息日志
    info(...args) {
        this._log(null, MonitorForWeb.levelEnum.info, ...args);
    }

    // 记录一条普通日志
    log(...args) {
        this.info(...args);
    }

    // 记录一条警告日志
    warn(...args) {
        this._log(null, MonitorForWeb.levelEnum.warn, ...args);
    }

    // 记录一条错误日志
    error(...args) {
        this._log(null, MonitorForWeb.levelEnum.error, ...args);
    }
    /* eslint-enable no-console, no-bitwise*/
}
// 日志级别枚举
MonitorForWeb.levelEnum = {
    /**
     * 信息
     */
    info: 'info',

    /**
     * 警告
     */
    warn: 'warn',

    /**
     * 错误
     */
    error: 'error',
};

// 日志颜色枚举
MonitorForWeb.colorEnum = {
    /**
     * 信息日志颜色，默认宝蓝色
     */
    info: 'DodgerBlue',

    /**
     * 警告日志颜色，默认橘黄色
     */
    warn: 'orange',

    /**
     * 错误日志颜色，默认红色
     */
    error: 'red',

    /**
     * ajax分组颜色，默认紫色
     */
    ajaxGroup: '#800080',

    /**
     * 日志发送成功颜色，默认绿色
     */
    sendSuccess: 'green',

    /**
     * 描述文字颜色，默认粉红色
     */
    desc: '#d30775',
};

// webWorker 返回状态枚举
MonitorForWeb.workerEnmu = {
    /**
     * 成功
     */
    done: 'DONE',

    /**
     * 重试
     */
    retry: 'RETRY',

    /**
     * 失败
     */
    failed: 'FAILED'
};
export default MonitorForWeb
